let events = {};

export default {
  register_listener(id, method, func) {
    if(!events[id]) events[id] = {}
    if(!events[id][method]) events[id][method] = []
    events[id][method].push(func)
  },
  install(Vue, options) {
    window.$socket = {};

    window.$socket.connecting = false;
    window.$socket.ready = false;

    window.$socket.send = function(event, method, data=undefined) {
      let to = undefined;
      if(data && data['to']) {
        to = data['to'];
        delete data['to'];

        if(data.length == 0)
          data = undefined;
      }

      window.$socket.proxy.send(JSON.stringify({event: event, method: method, to: to, payload: data}));
    };

    window.$socket.setProxy = function(proxy) {
      window.$socket.proxy = proxy;
    };


    window.$socket.setState = function(state) {
      console.log('Socket state: ' + state);

      if(state == 'connecting') {
        Vue.set(window.$socket, 'connecting', true);
        Vue.set(window.$socket, 'ready', false);
      }else if(state == 'ready') {
        Vue.set(window.$socket, 'connecting', false);
        Vue.set(window.$socket, 'ready', true);
      }else if(state == 'closed' || state == 'error') {
        Vue.set(window.$socket, 'connecting', false);
        Vue.set(window.$socket, 'ready', false);
      }
    };

    window.$socket.onMessage = function(data) {
      let event = data['event'];
      let method = data['method'];

      if(events[event]) {
        if(events[event][method]) {
          for(let func of events[event][method])
            func(data.payload);
        }
      }
    };

    // Set the default connection proxy.
    {
      let conn = null;
      let relay = {
        host: null,
        priority: 999999
      };

      // Use the current URL as the relay server.
      relay.host = location.href.split('/')[2].split(':')[0];

      // If a "?relay=" URL parameter has been defined, use that.
      var url = new URL(location.href);
      if(url.searchParams.get('relay'))
      relay.host = url.searchParams.get('relay');

      window.$socket.proxy = {
        default: true,
        relay,
        receivedBroadcast(ip, packet) {
          for(let service of packet.services) {
            if(service.id == 'RELAY') {
              if(!conn || service.payload.priority < relay.priority) {
                relay.host = ip;
                relay.priority = service.payload.priority;

                // Close the socket. The script should attempt to reconnect to the host automatically.
                if(conn)
                  conn.close();
              }
            }
          }
        },
        connect() {
          setTimeout(function() {
            if(!window.$socket.proxy.default) return;

            // Set the connection state for display
            window.$socket.setState('connecting');

            // Die if WebSockets are not supported.
            if(!window['WebSocket']) {
              console.error('Websockets not supported.');
            }

            console.log('Attempting to connect to ws://' + relay.host + ':29203/...');

            if(window.$socket.ready) conn.close();

            // Init the WebSocket connection
            conn = new WebSocket('ws://' + relay.host + ':29203/?subscribe={"*": ["data", "patch"], "machines": ["stats"]}');

            conn.onopen = function() {
              window.$socket.setState('ready');
            };

            conn.onerror = function() {
              window.$socket.setState('error');
            };

            conn.onclose = function() {
              window.$socket.setState('closed');
              window.$socket.proxy.connect();
            };

            conn.onmessage = function(e) { window.$socket.onMessage(JSON.parse(e.data)); };
          }, 1000);
        },
        send(message) {
          conn.send(message);
        }
      };

      window.$socket.proxy.connect();
    }

    Vue.mixin({
      data() {
        return {
          '$socket': window.$socket
        }
      }
    });

    Vue.prototype.$socket = window.$socket;
  }
};



