<template>
  <v-container fluid grid-list-md>
    <!-- Non compact layout start -->
    <div v-if="!compact">
      <v-layout row wrap>
        <v-flex xs12>
          <v-card class="px-1">
            <v-card-actions>
              <v-layout row wrap>
                <v-btn small outline block color="green" class="ma-1" @click="dialog.create.shown = true">Create Group</v-btn>
                <v-btn small outline block color="indigo" class="ma-1" @click="dialog.refresh.shown = true">Refresh All</v-btn>
                <v-btn small outline block color="blue" class="ma-1" @click="dialog.wake.shown = true">Wake All</v-btn>
                <v-btn small outline block color="orange" class="ma-1" @click="dialog.reboot.shown = true">Reboot All</v-btn>
                <v-btn small outline block color="red" class="ma-1" @click="dialog.shutdown.shown = true">Shutdown All</v-btn>
                <v-btn small flat class="ma-1" @click="compact = !compact">
                  <span v-if="compact">Compact</span>
                  <span v-else>Large</span>
                </v-btn>
              </v-layout>
            </v-card-actions>
          </v-card>
        </v-flex>
      </v-layout>

      <v-layout row wrap>
        <v-flex xs12 md6 v-for="(group, id) in groups" :key="id">
          <v-card>
            <v-card-title>
              <v-flex xs12 sm9>
                <router-link tag="h2" class="headline mb-0" style="cursor:pointer" :to="{ name: 'Group', params: {id} }">
                  {{ group.name }}
                </router-link>
              </v-flex>
              <v-flex xs4 sm1 class="text-xs-center">
                <v-btn small flat icon color="blue-grey" @click="dialog.group.id = id; dialog.group.shown = true">
                  <v-icon>settings</v-icon>
                </v-btn>
              </v-flex>
              <v-flex xs4 sm1 class="text-xs-center">
                <v-tooltip top>
                  <v-btn small flat icon slot="activator" @click="dialog.refresh.id = id; dialog.refresh.shown = true">
                    <v-icon class="indigo--text">sync</v-icon>
                  </v-btn>
                  <span>Refresh</span>
                </v-tooltip>
              </v-flex>
              <v-flex xs4 sm1 class="text-xs-center">
                <v-menu left>
                  <v-btn small flat icon color="primary" slot="activator"><v-icon>arrow_drop_down</v-icon></v-btn>
                  <v-list>
                    <v-list-tile @click="dialog.reboot.id = id; dialog.reboot.shown = true">
                      <v-list-tile-title><v-icon class="small blue--text">fa-power-off</v-icon> Reboot</v-list-tile-title>
                    </v-list-tile>
                    <v-list-tile @click="dialog.wake.id = id; dialog.wake.shown = true">
                      <v-list-tile-title><v-icon class="small orange--text">fa-heartbeat</v-icon> Wake Up</v-list-tile-title>
                    </v-list-tile>
                    <v-list-tile @click="dialog.shutdown.id = id; dialog.shutdown.shown = true">
                      <v-list-tile-title><v-icon class="small red--text">fa-plug</v-icon> Shutdown</v-list-tile-title>
                    </v-list-tile>
                    <v-list-tile @click="dialog.remove.id = id; dialog.remove.shown = true">
                      <v-list-tile-title><v-icon class="small">fa-trash</v-icon> Delete</v-list-tile-title>
                    </v-list-tile>
                  </v-list>
                </v-menu>
              </v-flex>
            </v-card-title>
            <v-layout row wrap class="pb-3 text-xs-center">
              <v-flex xs6 md3>
                <v-icon class="small" color="green">fiber_manual_record</v-icon> <b>Online</b>: {{ getOnlineMiners({group: id}) }}
              </v-flex>
              <v-flex xs6 md3>
                <v-icon class="small" color="red">fiber_manual_record</v-icon> <b>Offline</b>: {{ getOfflineMiners({group: id}) }}
              </v-flex>
              <v-flex xs6 md3>
                <v-icon class="small" color="orange">fa-thermometer-empty</v-icon> {{ convertTemperature(getMaxTemperature({group: id}), true) }}
              </v-flex>
              <v-flex xs6 md3>
                <v-icon class="small" color="orange">fa-hashtag</v-icon> {{ convertRate({ rate: getTotalRate({group: id}), display: true, algorithm: getAlgorithm({ group: id }) }) }}/s
              </v-flex>
            </v-layout>
          </v-card>
        </v-flex>
      </v-layout>
    </div>

    <!-- Compact layout start -->
    <v-layout row wrap v-else>
      <v-flex xs12>
        <v-card>
          <v-card-text>
            <v-layout row wrap>
              <v-flex xs12 sm4>
                Groups
              </v-flex>
              <v-flex xs3 sm1 class="text-xs-center">
                <v-tooltip top>
                  <v-btn slot="activator" small flat icon color="indigo" class="ma-1" @click="dialog.refresh.shown = true"><v-icon style="font-size: 1.5em">fa-refresh</v-icon></v-btn>
                  <span>Refresh All</span>
                </v-tooltip>
              </v-flex>
              <v-flex xs3 sm1 class="text-xs-center">
                <v-tooltip top>
                  <v-btn slot="activator" small flat icon color="blue" class="ma-1" @click="dialog.reboot.shown = true"><v-icon style="font-size: 1.5em">fa-power-off</v-icon></v-btn>
                  <span>Reboot All</span>
                </v-tooltip>
              </v-flex>
              <v-flex xs3 sm1 class="text-xs-center">
                <v-tooltip top>
                  <v-btn slot="activator" small flat icon color="orange" class="ma-1" @click="dialog.wake.shown = true"><v-icon style="font-size: 1.5em">fa-heartbeat</v-icon></v-btn>
                  <span>Wake All</span>
                </v-tooltip>
              </v-flex>
              <v-flex xs3 sm1 class="text-xs-center">
                <v-tooltip top>
                  <v-btn slot="activator" small flat icon color="red" class="ma-1" @click="dialog.shutdown.shown = true"><v-icon style="font-size: 1.5em">fa-plug</v-icon></v-btn>
                  <span>Shutdown All</span>
                </v-tooltip>
              </v-flex>
              <v-flex xs6 sm2 class="text-xs-center">
                  <v-btn small flat class="ma-1" @click="dialog.create.shown = true">
                    <span>New Group</span>
                  </v-btn>
              </v-flex>
              <v-flex xs6 sm2 class="text-xs-center">
                <v-btn small flat class="ma-1" @click="compact = !compact">
                  <span v-if="compact">Compact</span>
                  <span v-else>Large</span>
                </v-btn>
              </v-flex>
            </v-layout>
            <template v-for="(group, id) in groups">
              <v-container class="my-2" :key="'group-' + id">
                <v-divider class="mb-2" />
                <v-layout row wrap>
                  <v-flex xs8 sm5>
                    <router-link tag="div" style="cursor:pointer;font-size:1.2em" :to="{ name: 'Group', params: {id} }">
                      {{ group.name }}
                    </router-link>
                  </v-flex>
                  <v-flex xs2 sm1 md1 class="text-xs-center">
                    <v-icon class="small" color="green">fiber_manual_record</v-icon> {{ getOnlineMiners({group: id}) }}
                  </v-flex>
                  <v-flex xs2 sm1 md1 class="text-xs-center">
                    <v-icon class="small" color="red">fiber_manual_record</v-icon> {{ getOfflineMiners({group: id}) }}
                  </v-flex>
                  <v-flex xs3 sm1 md1 class="text-xs-center">
                    <v-icon class="small" color="orange">fa-thermometer-empty</v-icon> {{ convertTemperature(getMaxTemperature({group: id}), true) }}
                  </v-flex>
                  <v-flex xs3 sm2 md2 class="text-xs-center">
                    <v-icon class="small" color="orange">fa-hashtag</v-icon> {{ convertRate({ rate: getTotalRate({group: id}), display: true, algorithm: getAlgorithm({ group: id }) }) }}/s
                  </v-flex>
                  <v-flex xs3 sm1 class="text-xs-center">
                    <v-btn style="height:12px;width:12px" flat icon color="blue-grey" @click="dialog.group.id = id; dialog.group.shown = true">
                      <v-icon>settings</v-icon>
                    </v-btn>
                  </v-flex>
                  <v-flex xs3 sm1 class="text-xs-center">
                    <v-menu left>
                      <v-btn style="height:12px;width:12px" flat icon color="primary" slot="activator"><v-icon>arrow_drop_down</v-icon></v-btn>
                      <v-list>
                        <v-list-tile @click="dialog.refresh.id = id; dialog.refresh.shown = true">
                          <v-list-tile-title><v-icon class="small indigo--text">fa-refresh</v-icon> Refresh</v-list-tile-title>
                        </v-list-tile>
                        <v-list-tile @click="dialog.reboot.id = id; dialog.reboot.shown = true">
                          <v-list-tile-title><v-icon class="small blue--text">fa-power-off</v-icon> Reboot</v-list-tile-title>
                        </v-list-tile>
                        <v-list-tile @click="dialog.wake.id = id; dialog.wake.shown = true">
                          <v-list-tile-title><v-icon class="small orange--text">fa-heartbeat</v-icon> Wake Up</v-list-tile-title>
                        </v-list-tile>
                        <v-list-tile @click="dialog.shutdown.id = id; dialog.shutdown.shown = true">
                          <v-list-tile-title><v-icon class="small red--text">fa-plug</v-icon> Shutdown</v-list-tile-title>
                        </v-list-tile>
                        <v-list-tile @click="dialog.remove.id = id; dialog.remove.shown = true">
                          <v-list-tile-title><v-icon class="small">fa-trash</v-icon> Delete</v-list-tile-title>
                        </v-list-tile>
                      </v-list>
                    </v-menu>
                  </v-flex>
                </v-layout>
              </v-container>
            </template>
          </v-card-text>
        </v-card>
      </v-flex>
    </v-layout>

    <v-fab-transition>
      <v-speed-dial
        bottom
        right
        fixed
        direction="top"
        transition="slide-y-reverse-transition"
        v-show="fab.show"
        v-model="fab.dial">
        <v-btn dark fab hover color="primary darken-2" slot="activator" v-model="fab.dial">
          <v-icon>layers</v-icon>
          <v-icon>close</v-icon>
        </v-btn>
        <v-tooltip left>
          <v-btn fab dark small color="green" slot="activator" @click.stop="dialog.create.shown = true">
            <v-icon>add</v-icon>
          </v-btn>
          <span>Create Group</span>
        </v-tooltip>
        <v-tooltip left>
          <v-btn fab dark small color="indigo" slot="activator" @click.stop="dialog.refresh.shown = true">
            <v-icon>sync</v-icon>
          </v-btn>
          <span>Refresh All</span>
        </v-tooltip>
        <v-tooltip left>
          <v-btn fab dark small color="blue" slot="activator" @click.stop="dialog.wake.shown = true">
            <v-icon>wifi</v-icon>
          </v-btn>
          <span>Wake All</span>
        </v-tooltip>
        <v-tooltip left>
          <v-btn fab dark small color="orange" slot="activator" @click.stop="dialog.reboot.shown = true">
            <v-icon>refresh</v-icon>
          </v-btn>
          <span>Reboot All</span>
        </v-tooltip>
        <v-tooltip left>
          <v-btn fab dark small color="red" slot="activator" @click.stop="dialog.shutdown.shown = true">
            <v-icon>power</v-icon>
          </v-btn>
          <span>Shutdown All</span>
        </v-tooltip>
      </v-speed-dial>
    </v-fab-transition>

    <v-dialog v-model="dialog.group.shown" max-width="800px">
      <v-card v-if="dialog.group.id">
        <v-card-title>
          <h4 class="headline mb-0">{{ dialog.group.id ? groups[dialog.group.id].name : '' }}</h4>
        </v-card-title>
        <v-card-text>
          <group-settings :id="dialog.group.id"></group-settings>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primary" flat @click.stop="dialog.group.shown = false">Close</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-dialog v-model="dialog.create.shown" max-width="500px">
      <v-card>
        <v-card-title>
          <h4 class="headline mb-0">Create Group</h4>
          <small>
            A group is a collection of machines that all run the same mining software under the same configurations.
          </small>
        </v-card-title>
        <v-card-text>
          <v-text-field
            label="Group Title"
            clearable
            solo
            persistent-hint
            hint="This can be changed later"
            v-model="dialog.create.name">
          </v-text-field>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="grey" flat @click.stop="dialog.create.shown = false">Close</v-btn>
          <v-btn color="primary" flat @click.stop="dialog.create.shown = false; createGroup();">Create</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="dialog.remove.shown" max-width="400px">
      <v-card>
        <v-card-title>
          <h4 class="headline mb-0">Confirm{{ dialog.remove.id ? ': ' + groups[dialog.remove.id].name : '' }}</h4>
        </v-card-title>
        <v-card-text>
          Are you sure you want to delete that group?<br />
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="grey" flat @click.stop="dialog.remove.shown = false">No</v-btn>
          <v-btn color="primary" flat @click.stop="dialog.remove.shown = false; deleteGroup(dialog.remove.id)">Yes</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-dialog v-model="dialog.refresh.shown" max-width="400px">
      <v-card>
        <v-card-title>
          <h4 class="headline mb-0">Confirm{{ dialog.refresh.id ? ': ' + groups[dialog.refresh.id].name : '' }}</h4>
        </v-card-title>
        <v-card-text>
          Are you sure you want to refresh all {{ dialog.refresh.id ? 'of that group\s' : 'your' }} miners?
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="grey" flat @click.stop="dialog.refresh.shown = false">No</v-btn>
          <v-btn color="primary" flat @click.stop="dialog.refresh.shown = false; doGroupAction(dialog.refresh.id ? dialog.refresh.id : '*', 'refresh')">Yes</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="dialog.wake.shown" max-width="400px">
      <v-card>
        <v-card-title>
          <h4 class="headline mb-0">Confirm{{ dialog.wake.id ? ': ' + groups[dialog.wake.id].name : '' }}</h4>
        </v-card-title>
        <v-card-text>
          Are you sure you want to wake up all {{ dialog.wake.id ? 'of that group\s' : 'your' }} miners?<br />
          <small><b>Note:</b> This only works on machines with Wake-on-LAN enabled.</small>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="grey" flat @click.stop="dialog.wake.shown = false">No</v-btn>
          <v-btn color="primary" flat @click.stop="dialog.wake.shown = false; doGroupAction(dialog.wake.id ? dialog.wake.id : '*', 'wake')">Yes</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="dialog.reboot.shown" max-width="400px">
      <v-card>
        <v-card-title>
          <h4 class="headline mb-0">Confirm{{ dialog.reboot.id ? ': ' + groups[dialog.reboot.id].name : '' }}</h4>
        </v-card-title>
        <v-card-text>
          Are you sure you want to reboot all {{ dialog.reboot.id ? 'of that group\s' : 'your' }} miners?
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="grey" flat @click.stop="dialog.reboot.shown = false">No</v-btn>
          <v-btn color="primary" flat @click.stop="dialog.reboot.shown = false; doGroupAction(dialog.reboot.id ? dialog.reboot.id : '*', 'reboot')">Yes</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="dialog.shutdown.shown" max-width="400px">
      <v-card>
        <v-card-title>
          <h4 class="headline mb-0">Confirm{{ dialog.shutdown.id ? ': ' + groups[dialog.shutdown.id].name : '' }}</h4>
        </v-card-title>
        <v-card-text>
          Are you sure you want to shutdown all {{ dialog.shutdown.id ? 'of that group\s' : 'your' }} miners?<br />
          <small><b>Note:</b> You will have to turn them on manually if Wake-on-LAN is not enabled!</small>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="grey" flat @click.stop="dialog.shutdown.shown = false">No</v-btn>
          <v-btn color="primary" flat @click.stop="dialog.shutdown.shown = false; doGroupAction(dialog.shutdown.id ? dialog.shutdown.id : '*', 'shutdown')">Yes</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
  export default {
    data() {
      return {
        fab: {
          show: false,
          dial: false
        },
        dialog: {
          group: {
            shown: false,
            id: null
          },

          create: {
            shown: false,
            name: null
          },
          remove: {
            shown: false,
            id: null
          },

          refresh: {
            shown: false,
            id: null
          },
          wake: {
            shown: false,
            id: null
          },
          reboot: {
            shown: false,
            id: null
          },
          shutdown: {
            shown: false,
            id: null
          }
        }
      }
    },
    computed: {
      compact: {
        get() {
          return this.$store.state.groups_compact;
        }, set(val) {
          this.$store.commit('SET_GROUPS_COMPACT', val);
        }
      },
      groups() {
        return this.$store.state.groups;
      }
    },
    watch: {
      'dialog.group.shown': function(val, oldVal) {
        if(!val) {
          let vm = this;
          setTimeout(() => {
            vm.dialog.group.id = null;
          }, 200);
        }
      },

      'dialog.create.shown': function(val, oldVal) {
        if(!val) {
          let vm = this;
          setTimeout(() => {
            vm.dialog.create.name = null;
          }, 200);
        }
      },
      'dialog.remove.shown': function(val, oldVal) {
        if(!val) {
          let vm = this;
          setTimeout(() => {
            vm.dialog.remove.id = null;
          }, 200);
        }
      },

      'dialog.refresh.shown': function(val, oldVal) {
        if(!val) {
          let vm = this;
          setTimeout(() => {
            vm.dialog.refresh.id = null;
          }, 200);
        }
      },
      'dialog.wake.shown': function(val, oldVal) {
        if(!val) {
          let vm = this;
          setTimeout(() => {
            vm.dialog.wake.id = null;
          }, 200);
        }
      },
      'dialog.reboot.shown': function(val, oldVal) {
        if(!val) {
          let vm = this;
          setTimeout(() => {
            vm.dialog.reboot.id = null;
          }, 200);
        }
      },
      'dialog.shutdown.shown': function(val, oldVal) {
        if(!val) {
          let vm = this;
          setTimeout(() => {
            vm.dialog.shutdown.id = null;
          }, 200);
        }
      }
    },
    mounted() {
      let vm = this;
      setTimeout(() => { vm.fab.show = true }, 500);
    },
    methods: {
      createGroup() {
        if(!this.dialog.create.name) return;
        this.$socket.send('groups', 'new', {name: this.dialog.create.name});
      }, deleteGroup(id) {
        this.$socket.send('groups', 'delete', this.dialog.remove.id);
      }
    }
  }
</script>



