<template>
    <div class="status" :style="'height: ' + (large ? 32 : 24) + 'px;'">
        <v-tooltip top>
            <div slot="activator">
                <v-icon :large="large" :class="base_color + '--text'" slot="activator">label</v-icon>
                <v-icon :large="large" class="unsynced" v-if="!is_synced">label</v-icon>
                <v-icon :large="large" class="fee" v-if="miner_stats && miner_stats.status.fee">label</v-icon>
            </div>
            <span>{{ base_text }}</span>
            <span v-if="miner_stats && miner_stats.status.fee">Fee</span>
            <span v-if="!is_synced">(Not Synced)</span>
        </v-tooltip>
    </div>
</template>

<style>
  .status {
    
  }

  .status .icon {
    float: left;
    margin-bottom: -100%;
  }

  .unsynced {
    background: linear-gradient(135deg, rgba(0, 0, 0, 0) 50%, rgba(0, 0, 0, 0), #FFC107, #FFC107 50%);
    background-clip: border-box;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }

  .fee {
    background: radial-gradient(#FFF 20%, rgba(0, 0, 0, 0) 20%);
    background-clip: border-box;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
</style>

<script>
  export default {
    name: 'miner-status',
    props: ['miner_id', 'large'],
    computed: {
      is_synced() {
        return this.isSynced(this.miner.config.group.id, this.miner);
      },
      miner() {
        return this.$store.state.machines[this.miner_id];
      },
      miner_stats() {
        return this.$store.state.miner_stats[this.miner_id];
      },

      base_color() {
        if(!this.miner_stats || !this.miner_stats.connected) return 'grey';
        if(this.miner_stats.status.type == 'offline') return 'error';
        if(this.miner_stats.status.type == 'mining') return this.miner.config.dummy ? 'primary' : 'success';
        if(this.miner_stats.status.type == 'online') return 'success';
        return 'black';
      }, base_text() {
        if(!this.miner_stats || !this.miner_stats.connected) return 'Offline';
        if(this.miner_stats.status.type == 'offline') return 'Not Mining';
        if(this.miner_stats.status.type == 'mining') return this.miner.config.dummy ? 'Monitoring' : 'Mining';
        if(this.miner_stats.status.type == 'online') return 'Starting Up';
        return 'black';
      }
    },
  }
</script>


