<template>
  <v-container fluid grid-list-md>
    <v-layout row wrap>
      <v-flex xs12 sm6>
        <v-flex xs12>
          <v-card>
            <v-card-text>
              <v-layout row wrap>
                <v-flex sm6>
                  <label>Temperature</label>
                  <v-radio-group v-model="temperature">
                    <v-radio label="Kelvin" value="K"></v-radio>
                    <v-radio label="Celsius" value="C"></v-radio>
                    <v-radio label="Fahrenheit" value="F"></v-radio>
                  </v-radio-group>
                </v-flex>
                <v-flex sm6>
                  <v-select
                    :items="tickers_data"
                    item-text="name"
                    item-value="_id"
                    v-model="currency"
                    label="Currency"
                    autocomplete
                    single-line
                    bottom>
                  </v-select>
                  <v-switch
                    label="Mini Sidebar"
                    v-model="sidebarMini"
                  ></v-switch>
                </v-flex>
              </v-layout>
            </v-card-text>
          </v-card>
        </v-flex>
        <v-flex xs12>
          <v-card>
            <v-card-text primary-title>
              <h4 class="headline mb-0">Upgrade Ivy</h4>
            </v-card-text>
            <v-card-text>
              <p style="text-align:justify">
                Attempt to upgrade Ivy on all your farm's mining rigs. Support will only be provided for the latest stable version, however, a beta version is available if you would like to help test new features, and a bleeding edge option is provided if you want the latest development version.
              </p>
              <p style="text-align:justify">
                <b>Note:</b> This will not upgrade your master and storage servers, only your mining machines. Upgrades to other systems must be done through the <i>Network</i> page.
              </p>
            </v-card-text>
            <table class="table">
              <tbody>
                <tr v-for="version, id in versions" v-if="version.commit">
                  <td><b>{{ version.name }}:</b></td>
                  <td><code>{{ version.version }}</code></td>
                  <td class="text-xs-right"><v-btn outline :color="version.color" @click="dialog.shown = true; dialog.version = id">Upgrade</v-btn></td>
                </tr>
              </tbody>
            </table>
          </v-card>
        </v-flex>
      </v-flex>
      <v-flex xs12 sm6>
        <v-card>
          <v-card-text primary-title>
            <h4 class="headline mb-0">Usage Fee</h4>
          </v-card-text>
          <v-card-text>
            <p style="text-align:justify">
              This software is <i>distributed</i> free of charge, however, in order to support the creators and allow future development,
              a "usage fee" is implemented. It results in approximately <b>{{ (feePercent * 100).toFixed(2) }}%</b> being sent
              to the developers. We kindly ask that you keep the fee intact.
            </p>
            <p style="text-align:justify">
              If the collection interval is inconvenient for you, you can change it below. Higher values may mess with your statistics,
              lower values are harder on your machine. I recommend every 4 or 6 hours if possible.
            </p>
            <p style="text-align:justify">
              The usage fee is currently as such: for <b>{{ feeTimeString(fee.interval) }}</b>, <b>every {{ fee.interval }} hours</b> of uptime,
              machines pause and switch to mine using the team's address.
            </p>
            <v-select
              :items="[
                {value: 2, text: feeTimeString(2) + ', every 2 hours'},
                {value: 4, text: feeTimeString(4) + ', every 4 hours'},
                {value: 6, text: feeTimeString(6) + ', every 6 hours'},
                {value: 8, text: feeTimeString(8) + ', every 8 hours'},
                {value: 12, text: feeTimeString(12) + ', every 12 hours'},
                {value: 24, text: feeTimeString(24) + ', every 24 hours'}
              ]"
              item-text="text"
              item-value="value"
              v-model="fee.interval"
              label="Usage Fee Interval"
              persistent-hint
              hint="Changes require a refresh of all machines to take effect."
              @change="updateFee()">
            </v-select>
          </v-card-text>
        </v-card>
          <v-card>
            <v-card-text primary-title>
              <h4 class="headline mb-0">Testing</h4>
            </v-card-text>
            <v-card-text>
              <v-btn outline color="green" @click="newMessage({level: 'success', text: 'This is a test success notification.'})">Test Success Notification</v-btn>
              <v-btn outline color="blue" @click="newMessage({level: 'info', text: 'This is a test info notification.'})">Test Info Notification</v-btn>
              <v-btn outline color="yellow" @click="newMessage({level: 'warning', text: 'This is a test warning notification.'})">Test Warning Notification</v-btn>
              <v-btn outline color="red" @click="newMessage({level: 'danger', text: 'This is a test danger notification.'})">Test Danger Notification</v-btn>
              <v-btn outline color="grey" @click="newMessage({level: 'bug', text: 'This is a test bug notification.'})">Test Bug Notification</v-btn>
            </v-card-text>
          </v-card>
      </v-flex>
    </v-layout>

    <v-dialog v-model="dialog.shown" max-width="400px">
      <v-card>
        <v-card-title>
          <h4 class="headline mb-0">Confirm</h4>
        </v-card-title>
        <v-card-text v-if="dialog.version">
          Are you sure you want to update all machines to <b>{{ versions[dialog.version].name }}</b> <code>{{ versions[dialog.version].version }}</code>?
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="grey" flat @click.stop="dialog.shown = false">No</v-btn>
          <v-btn color="primary" flat @click.stop="dialog.shown = false; doGlobalUpgrade(versions[dialog.version])">Yes</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
  export default {
    data() {
      return {
        fee_slider: 0,

        dialog: {
          shown: false,
          version: null
        },

        versions: {
          stable: {
            color: 'success',
            name: 'Stable',
            version: null,
            commit: null
          },
          beta: {
            color: 'warning',
            name: 'Beta',
            version: null,
            commit: null
          },
          bleeding: {
            color: 'error',
            name: 'Bleeding Edge',
            version: null,
            commit: null
          }
        }
      }
    },
    computed: {
      temperature: {
        get() {
          return this.$store.state.temperature;
        }, set(val) {
          this.$store.commit('SET_TEMPERATURE', val);
        }
      },
      currency: {
        get() {
          return this.$store.state.currency;
        }, set(val) {
          this.$store.commit('SET_CURRENCY', val);
        }
      },
      sidebarMini: {
        get() {
          return this.$store.state.sidebar.mini;
        }, set(val) {
          this.$store.commit('SET_SIDEBAR_MINI', val);
        }
      },

      tickers_data() {
        let tickers = [{_id: null, name: 'BTC'}];
        for(let id in this.$store.state.tickers)
          tickers.push({_id: id, name: id});
        return tickers;
      },

      fee() {
        return this.$store.state.fee;
      },
      feePercent() {
        return this.fee.daily / (24 * 60);
      }
    },
    mounted() {
      this.$http.get('https://api.github.com/repos/Secret-Web/ivy/tags').then(response => {
        for(let release of response.body) {
          if(release.name.indexOf('-beta') == -1) {
            if(!this.versions.stable.commit) {
              this.versions.stable.version = release.name;
              this.versions.stable.commit = release.commit.sha;
            }
          }else{
            if(!this.versions.beta.commit) {
              this.versions.beta.version = release.name;
              this.versions.beta.commit = release.commit.sha;
            }
          }
        }
      }, response => { });
      this.$http.get('https://api.github.com/repos/Secret-Web/ivy/commits').then(response => {
        let bleeding = response.body[0];
        this.versions.bleeding.version = bleeding.commit.author.date;
        this.versions.bleeding.commit = bleeding.sha;
      }, response => { });
    },
    methods: {
      doGlobalUpgrade(version) {
        let v = {to: 'one'}; v['*'] = {id: 'upgrade', version};
        this.$socket.send('groups', 'action', v);
      },

      feeTimeString(fee) {
        let ret = [];

        let time = (fee / 24) * this.fee.daily;

        if(time >= 1) {
          ret.push(Math.floor(time) + ' minutes');
          time -= Math.floor(time);
        }

        if(time > 0) {
          ret.push((time * 60).toFixed(0) + ' seconds')
        }

        return ret.join(' and ');
      },
      updateFee() {
        this.$nextTick(function() {
          this.$socket.send('fee', 'set', this.fee);
        });
      },
      newMessage(msg) {
        this.$socket.send('messages', 'new', msg);
      }
    }
  }
</script>



