Vue.prototype.getOnlineGPUs = function(opt={}) {
  let t = 0;
  if(!opt.miner) {
    for(let id in store.state.machines) {
      let miner = store.state.machines[id];
      if(opt.group) {
        if(!miner.config) continue;
        if(!miner.config.group) continue;
        if(miner.config.group.id != opt.group) continue;
      }

      let stats = store.state.miner_stats[id];
      if(!stats || !stats.hardware || !stats.hardware.gpus) continue;

      for(let j in stats.hardware.gpus)
        t += (stats.hardware.gpus[j].status.type == 'online' ? 1 : 0);
    }
  }else{
    let stats = store.state.miner_stats[opt.miner];
    if(!stats || !stats.hardware || !stats.hardware.gpus) return 0;

    for(let i in stats.hardware.gpus)
      t += (stats.hardware.gpus[i].status.type == 'online' ? 1 : 0);
  }
  return t;
};

