<template>
  <v-card>
    <v-card-title>
      <v-layout row wrap>
        <v-flex xs12 sm8>
          <h4 class="headline mb-0">{{ title }}<small>{{ total ? ': ' + total : '' }}</small></h4>
          <span>{{ info }}</span>
        </v-flex>
        <v-flex xs12 sm4 class="text-xs-center text-sm-right">
          <v-menu
            offset-y
            max-height="200px">
            <v-btn small flat color="primary" slot="activator">
              <i class="fa fa-history"></i> &nbsp; {{ increments[increment][1] }}
            </v-btn>
            <v-list dense>
              <v-list-tile v-for="(data, i) in increments" :key="i" @click="increment = i; update(true)">
                <v-list-tile-title>{{ data[1] }}</v-list-tile-title>
              </v-list-tile>
            </v-list>
          </v-menu>
        </v-flex>
      </v-layout>
    </v-card-title>
    <div v-if="state == 'loading'" class="text-xs-center pa-3">
      <v-progress-circular indeterminate color="primary"></v-progress-circular>
    </div>
    <div v-else-if="state == 'incomplete'" class="text-xs-center pa-3">
      Not enough data to display that time frame.
    </div>
    <div class="pr-2" :style="'display:' + (state == 'done' ? 'block' : 'none') + ';height:' + (height !== undefined ? height : '300') + 'px'">
      <vue-chart
        ref="vuechart"
        type="line"
        :data="chartData"
        :options="chartOptions" />
    </div>
  </v-card>
</template>

<script>
  const AVG_SIZE = 2;

  export default {
    name: 'statistics',
    props: ['height', 'small', 'title', 'info', 'id', 'type', 'machine_id'],
    data() {
      return {
        increment: (!localStorage['stats-' + this.id] ? (this.increment ? this.increment : 1) : localStorage['stats-' + this.id]),
        increments: [
          ['5m', '5 minutes'],
          ['10m', '10 minutes'],
          ['30m', '30 minutes'],
          ['1hr', 'Hourly'],
          ['6hr', '6 hours'],
          ['12hr', '12 hours'],
          ['1d', 'Daily'],
          ['7d', 'Weekly']
        ],

        state: 'loading',
        loop: null,

        total: null,

        chartGradient: null,
        chartData: {},
        chartOptions: {
          responsive: true,
          maintainAspectRatio: false,
          legend: {
            display: false,
          },
          scales: {
            xAxes: [{
              ticks: {
                fontSize: 10,
                maxRotation: 0,
                minRotation: 0,
                callback: function(value, index, values) {
                  return (index != 0 && index != values.length - 1 ? value : '');
                }
              }
            }],
            yAxes: [{
              ticks: {
                max: 100,
                min: 0,
                beginAtZero: false,
                callback: function(value, index, values) {
                  return (index != 0 && index != values.length - 1 ? value : '');
                }
              }
            }]
          }
        }
      }
    },
    computed: {
      statistics() {
        return this.$store.state.statistics[this.id];
      }
    },
    watch: {
      increment(val, oldVal) {
        localStorage['stats-' + this.id] = val;
        this.initLoop();
      },
      'statistics':  {
        handler(val, oldVal) {
          let labels = [];
          let series = [];

          let max_amt = [0, ''];

          this.total = 0;

          for(let i in val) {
            // if(i == val.length - 1) continue;
            let [date, stats] = val[i];

            date = new Date(date * 1000);

            if(this.increment > 5)
             labels.push(date.toLocaleDateString([], { month: 'short', day: 'numeric' }));
            else
              labels.push(date.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' }));

            if(stats == null) {
              series.push(null);
              continue;
            }

            if(this.type == 'gpus') {
              series.push(stats['gpus'] > 0 ? stats['gpus'] : null);
              this.total += stats['gpus'];
            }else if(this.type == 'rate') {
              if(stats['rate'] < 0) {
                console.error('Rate is a negative number!');
                continue;
              }

              if(stats['rate'] > max_amt[0]) {
                max_amt = this.convertRate({ rate: stats['rate'] });
                max_amt[0] = stats['rate'];
              }

              series.push(stats['rate'] > 0 ? stats['rate'] : 0);
            }

            else if(this.type == 'invalid') {
              if(stats['shares']['invalid'] < 0) {
                console.error('Invalid share count is a negative number!');
                continue;
              }

              series.push(stats['shares']['invalid'] > 0 ? stats['shares']['invalid'] : null);
              this.total += stats['shares']['invalid'];
            }else if(this.type == 'accepted') {
              if(stats['shares']['accepted'] < 0) {
                console.error('Accepted share count is a negative number!');
                continue;
              }

              series.push(stats['shares']['accepted'] > 0 ? stats['shares']['accepted'] : null);
              this.total += stats['shares']['accepted'];
            }else if(this.type == 'rejected') {
              if(stats['shares']['rejected'] < 0) {
                console.error('Rejected share count is a negative number!');
                continue;
              }

              series.push(stats['shares']['rejected'] > 0 ? stats['shares']['rejected'] : null);
              this.total += stats['shares']['rejected'];
            }
          }

          if(this.total == 0)
            this.total = null;


          if(this.type == 'rate') {
            this.total = this.convertRate({ rate: max_amt[0], display: true, minimum: max_amt[1] }) + '/s';
            for(let i in series) {
              if(series[i] == null) continue;
              series[i] = parseFloat(this.convertRate({ rate: series[i], display: false, minimum: max_amt[1] })[0].toFixed(2));
            }
          }


          let seriesAverage = [];
          for(let i in series) {
            if(!series[i]) continue;

            let sum = 0;
            let num = 0;
            for(let j = Math.max(i - AVG_SIZE, 0); j < Math.min(parseInt(i) + AVG_SIZE + 1, series.length); j++) {
              if(!series[j]) continue;
              num += 1;
              sum += series[j];
            }

            seriesAverage[i] = parseFloat((sum / num).toFixed(2));
          }

          labels = labels.slice(AVG_SIZE, labels.length);
          seriesAverage = seriesAverage.slice(AVG_SIZE, seriesAverage.length);
          series = series.slice(AVG_SIZE, series.length);


          let min = Number.MAX_VALUE;
          let max = Number.MIN_VALUE;
          for(let point of seriesAverage) {
            if(!point) continue;
            if(point < min) min = point;
            if(point > max) max = point;
          }
          for(let point of series) {
            if(!point) continue;
            if(point < min) min = point;
            if(point > max) max = point;
          }

          let deviation = Math.max(5, max - min);

          this.chartOptions.scales.yAxes[0].ticks.min = Math.ceil(Math.max(0, min - deviation / 1.5));
          this.chartOptions.scales.yAxes[0].ticks.max = Math.ceil(max + deviation / 1.5);


          this.chartData = {
            labels: labels,
            datasets: [
              {
                type: 'line',
                label: 'Average ' + this.title,
                data: seriesAverage,
                fill: false,
                backgroundColor: "#FFF"
              }, {
                label: this.title,
                backgroundColor: this.chartGradient,
                data: series
              }
            ]
          };

          this.state = series.length < 2 ? 'incomplete' : 'done';
        },
        deep: true
      }
    },
    methods: {
      initLoop() {
        let vm = this;

        if(this.loop != null)
          clearInterval(this.loop);

        this.loop = setInterval(function() {
          vm.update(true);
        }, 1000 * 60 * 2.5);
      },
      update(silent=false) {
        if(!silent) {
          this.state = 'loading';
          this.total = null;
        }

        this.$socket.send('stats', 'query', {
          'id': this.id,
          'increment': this.increments[this.increment][0],
          machine_id: this.machine_id
        });
      }
    },
    mounted() {
      let ctx = this.$refs.vuechart.chart.canvas.getContext('2d');
      this.chartGradient = ctx.createLinearGradient(0, 0, 0, (this.height !== undefined ? this.height : 300));
      this.chartGradient.addColorStop(0, 'rgb(244, 67, 54, .8)');
      this.chartGradient.addColorStop(1, 'rgb(193, 119, 93, .7)');

      this.initLoop();

      this.update();
    },
    destroyed() {
      clearInterval(this.loop);
    }
  }
</script>



