<template>
  <v-container fluid grid-list-md>
    <v-layout row wrap>
      <v-flex md6 lg3>
        <v-card>
          <v-card-text>
            <v-layout row wrap>
              <v-flex md4>
                <v-icon class="success--text" style="font-size:4rem" v-if="getOfflineMiners() == 0">check</v-icon>
                <v-icon class="error--text" style="font-size:4rem" v-else>close</v-icon>
              </v-flex>
              <v-flex md8>
                <b>Miners Online</b>
                <v-divider class="my-2"></v-divider>
                {{ getOnlineMiners() }}/{{ getTotalMiners() }}
              </v-flex>
            </v-layout>
          </v-card-text>
        </v-card>
      </v-flex>
      <v-flex md6 lg3>
        <v-card>
          <v-card-text>
            <v-layout row wrap>
              <v-flex md4>
                <v-icon class="success--text" style="font-size:4rem" v-if="getOfflineGPUs() == 0">check</v-icon>
                <v-icon class="error--text" style="font-size:4rem" v-else>close</v-icon>
              </v-flex>
              <v-flex md8>
                <b>GPUs Online</b>
                <v-divider class="my-2"></v-divider>
                {{ getOnlineGPUs() }}/{{ getTotalGPUs() }}
              </v-flex>
            </v-layout>
          </v-card-text>
        </v-card>
      </v-flex>
      <v-flex md6 lg3>
        <v-card>
          <v-card-text>
            <v-layout row wrap>
              <v-flex md4>
                <v-icon class="success--text" style="font-size:4rem" v-if="machinesUnassigned == 0">check</v-icon>
                <v-icon class="error--text" style="font-size:4rem" v-else>close</v-icon>
              </v-flex>
              <v-flex md8>
                <b>Unassigned Miners</b>
                <v-divider class="my-2"></v-divider>
                {{ machinesUnassigned }}
              </v-flex>
            </v-layout>
          </v-card-text>
        </v-card>
      </v-flex>
      <v-flex md6 lg3>
        <v-card>
          <v-card-text>
            <v-layout row wrap>
              <v-flex md4>
                <v-icon class="success--text" style="font-size:4rem" v-if="machinesUnsynced == 0">check</v-icon>
                <v-icon class="error--text" style="font-size:4rem" v-else>close</v-icon>
              </v-flex>
              <v-flex md8>
                <b>Unsynced Miners</b>
                <v-divider class="my-2"></v-divider>
                {{ machinesUnsynced }}
              </v-flex>
            </v-layout>
          </v-card-text>
        </v-card>
      </v-flex>
    </v-layout>
    <v-layout row wrap>
      <v-flex xs12 md3>
        <v-card>
          <v-list dense>
            <v-list-tile  @click="filter = null">
              <v-list-tile-avatar>
                <v-icon>select_all</v-icon>
              </v-list-tile-avatar>
              <v-list-tile-content>
                <v-list-tile-title>All</v-list-tile-title>
              </v-list-tile-content>
            </v-list-tile>
            <template v-for="(level, id) in levels">
              <v-list-tile :key="id" @click="filter = id">
                <v-list-tile-avatar>
                  <v-icon :class="level.color ? level.color + '--text' : ''">{{ level.icon }}</v-icon>
                </v-list-tile-avatar>
                <v-list-tile-content>
                  <v-list-tile-title>
                    {{ level.name }}
                  </v-list-tile-title>
                </v-list-tile-content>
                <v-list-tile-action v-if="lenLevel(id) > 0">
                  <v-btn icon ripple @click="dialog.delete.level = id; dialog.delete.shown = true">
                    {{ lenLevel(id) }}
                  </v-btn>
                </v-list-tile-action>
              </v-list-tile>
            </template>
          </v-list>
        </v-card>
      </v-flex>
      <v-flex xs12 md9>
        <v-card>
          <v-card-text>
            <center class="pa-3" v-if="messages == 0">No notifications to show. That'll probably change, soon.</center>
            <v-layout row wrap v-else v-for="[i, msg] in messages" :key="i">
              <v-flex xs2 sm1 class="text-xs-center">
                <v-icon style="padding:10px 0 8px" v-if="msg.level && msg.level in levels" :class="levels[msg.level].color ? levels[msg.level].color + '--text' : ''">{{ levels[msg.level].icon }}</v-icon>
              </v-flex>
              <v-flex xs10 sm8>
              <small>{{ formatTime(msg.time) }}</small>
              <br />
                {{ msg.text }}
              </v-flex>
              <v-flex offset-xs3 offset-sm0 xs3 sm2 class="text-xs-right">
                <span v-if="msg.machine">
                  <router-link v-if="machines.hasOwnProperty(msg.machine)" :to="{ name: 'Miner', params: {id: msg.machine} }">
                    <v-btn icon ripple style="margin:0">
                      <v-icon class="blue-grey--text">dns</v-icon>
                    </v-btn>
                  </router-link>
                  <v-btn icon ripple disabled style="margin:0" v-else>
                    <v-icon class="blue-grey--text">dns</v-icon>
                  </v-btn>
                </span>
                <span v-if="msg.group">
                  <router-link v-if="groups.hasOwnProperty(msg.group)" :to="{ name: 'Group', params: {id: msg.group} }">
                    <v-btn icon ripple style="margin:0">
                      <v-icon class="light-green--text">layers</v-icon>
                    </v-btn>
                  </router-link>
                  <v-btn icon ripple disabled style="margin:0" v-else>
                    <v-icon class="light-green--text">layers</v-icon>
                  </v-btn>
                </span>
              </v-flex>
              <v-flex xs3 sm1 class="text-xs-center">
                <v-btn icon ripple style="margin:0" @click="deleteMessage(i)">
                  <v-icon class="grey--text">close</v-icon>
                </v-btn>
              </v-flex>
            </v-layout>
          </v-card-text>
        </v-card>
      </v-flex>
    </v-layout>

    <v-dialog v-model="dialog.delete.shown" max-width="400px">
      <v-card>
        <v-card-title>
          <h4 class="headline mb-0">Confirm</h4>
        </v-card-title>
        <v-card-text>
          Are you sure you want to delete all messages in that level?
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="grey" flat @click.stop="dialog.delete.shown = false">No</v-btn>
          <v-btn color="primary" flat @click.stop="dialog.delete.shown = false; deleteMessages(dialog.delete.level)">Yes</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
  export default {
    data() {
      return {
        filter: null,
        levels: {
          success: {
            name: 'Success',
            icon: 'check',
            color: 'success'
          },
          info: {
            name: 'Information',
            icon: 'info',
            color: 'info'
          },
          warning: {
            name: 'Warning',
            icon: 'warning',
            color: 'yellow'
          },
          danger: {
            name: 'Danger',
            icon: 'flash_on',
            color: 'error'
          },
          bug: {
            name: 'Bug',
            icon: 'bug_report'
          }
        },
        dialog: {
          delete: {
            shown: false,
            level: null
          }
        }
      }
    },
    computed: {
      messages() {
        let messages = [];
        for(let [i, message] of Object.entries(this.$store.state.messages)) {
          if(this.machine && message.machine != this.machine) continue;
          if(this.filter != null && this.filter != message.level) continue;
          messages.push([parseInt(i), message]);
        }
        return messages;
      },

      groups() {
        return this.$store.state.groups;
      },

      machines() {
        return this.$store.state.machines;
      },

      machinesUnassigned() {
        let t = 0;
        for(let [i, machine] of Object.entries(this.machines))
          t += machine.config.group && machine.config.group.id ? 0 : 1;
        return t;
      },
      machinesUnsynced() {
        return this.getUnsyncedMiners();
      }
    },
    watch: {
      'dialog.delete.shown': function(val, oldVal) {
        if(!val) {
          let vm = this;
          setTimeout(() => {
            vm.dialog.delete.level = null;
          }, 200);
        }
      },
    },
    methods: {
      lenLevel(level) {
        let t = 0;
        for(let msg of this.$store.state.messages)
          t += level == null || msg.level == level ? 1 : 0
        return t;
      },
      formatTime(time) {
        let date = new Date(time.replace(/-/g, '/') + ' UTC');
        console.log(date);
        return date.toLocaleDateString([], { month: 'short', day: 'numeric', hour: 'numeric', minute: 'numeric' });
      },
      deleteMessage(i) {
        this.$socket.send('messages', 'delete', i);
      },
      deleteMessages(level) {
        this.$socket.send('messages', 'delete', level);
      }
    }
  }
</script>



