<template>
  <div>
    <v-layout row wrap>
      <v-flex xs6 sm4 class="pr-1">
        <v-select
          clearable
          label="Pool"
          item-text="name"
          item-value="_id"
          :items="pools_data"
          v-model="group.pool.id"
          @change="pushUpdate()">
        </v-select>
      </v-flex>

      <v-flex xs6 sm4 class="px-1">
        <v-select
          clearable
          :disabled="!group.pool || !pools.hasOwnProperty(group.pool.id)"
          label="Endpoint"
          item-text="name"
          item-value="_id"
          :items="pool_endpoints_data"
          v-model="group.pool.endpoint"
          @change="pushUpdate()">
        </v-select>
      </v-flex>

      <v-flex sm4 class="pl-1">
        <v-select
          clearable
          :label="'Wallet (' + (isValidConfig && this.pools[this.group.pool.id].endpoints[this.group.pool.endpoint].username ? 'optional' : 'required') + ')'"
          item-text="_name"
          item-value="_id"
          :items="wallets_data"
          v-model="group.wallet"
          @change="pushUpdate()">
        </v-select>
      </v-flex>
    </v-layout>
    <br />
    <v-layout row wrap>
      <v-flex sm4 class="pr-1">
        <v-select
          clearable
          label="Mining Software"
          item-text="name"
          item-value="_id"
          :items="software_data"
          v-model="group.program.id"
          @change="changedProgram()">
        </v-select>
      </v-flex>
      <v-flex sm8 class="pl-1">
        <div v-if="software.hasOwnProperty(group.program.id) && group.program.execute">
          <v-text-field
            multi-line
            label="Command Template"
            style="resize:vertical"
            v-model="group.program.execute.args"
            @blur="pushUpdate()">
          </v-text-field>
        </div>
      </v-flex>
    </v-layout>
    <v-layout row wrap v-if="software.hasOwnProperty(group.program.id)">
      <v-flex sm12>
        <v-card
          raised>
          <v-card-text>
            <div style="line-height:2em" v-html="realGroupExec"></div>
          </v-card-text>
        </v-card>
        <br />
        Legend: <span class="label orange">Switch Value</span> <span class="label green">Special Value</span> <span class="label blue">Tag Value</span>
      </v-flex>
    </v-layout>
  </div>
</template>

<style>
  .label {
    padding: 2px 5px;

    color: #FFF;
    font-weight: 600;

    border-radius: 4px;
  }
</style>

<script>
  export default {
    name: 'group-settings',
    props: ['id'],
    computed: {
      group() {
        return this.$store.state.groups[this.id];
      },

      coins() {
        return this.$store.state.coins;
      },

      software() {
        return this.$store.state.software;
      },
      software_data() {
        let programs = [];
        for(let [id, software] of Object.entries(this.software)) {
          if(this.groupCrypto && software.algorithm.indexOf(this.coins[this.groupCrypto].algorithm) == -1) continue;
          programs.push(Object.assign({_id: id}, software));
        }
        return programs;
      },

      pools() {
        return this.$store.state.pools;
      },
      pools_data() {
        let pools = [];
        for(let [id, pool] of Object.entries(this.pools))
          pools.push(Object.assign({_id: id}, pool));
        return pools;
      },
      pool_endpoints_data() {
        let endpoints = [];
        if(this.pools.hasOwnProperty(this.group.pool.id))
          for(let [id, endpoint] of Object.entries(this.pools[this.group.pool.id].endpoints)) {
            if(this.groupCrypto && this.groupCrypto != endpoint.crypto) continue;
            endpoints.push(Object.assign({_id: id}, endpoint));
          }
        return endpoints;
      },

      wallets() {
        return this.$store.state.wallets;
      },
      wallets_data() {
        let wallets = [];
        for(let [id, wallet] of Object.entries(this.wallets)) {
          if(wallet.crypto && this.groupCrypto && this.groupCrypto != wallet.crypto) continue;
          wallets.push(Object.assign({_id: id, _name: (this.groupCrypto == null ? '[' + wallet.crypto + '] ' : '') + wallet.name}, wallet));
        }
        return wallets;
      },

      program() {
        return this.software[this.group.program.id];
      },
      groupCrypto() {
        let crypto = null;

        if(this.group.pool && this.group.pool.id && this.group.pool.endpoint) {
          if(this.pools.hasOwnProperty(this.group.pool.id)) {
            if(this.pools[this.group.pool.id].endpoints.hasOwnProperty(this.group.pool.endpoint)) {
              crypto = this.pools[this.group.pool.id].endpoints[this.group.pool.endpoint].crypto;
            }
          }
        }

        if(!crypto && this.group.wallet) {
          if(this.wallets.hasOwnProperty(this.group.wallet)) {
            crypto = this.wallets[this.group.wallet].crypto;
          }
        }

        return crypto;
      },

      isValidConfig() {
        return this.group.pool && this.group.pool.id && this.group.pool.endpoint && this.pools.hasOwnProperty(this.group.pool.id) && this.pools[this.group.pool.id].endpoints.hasOwnProperty(this.group.pool.endpoint);
      },

      realGroupExec() {
        if(!this.program || !this.group.program.execute || !this.group.program.execute.args) return '';

        let exec = this.group.program.execute.args;

        if(this.group.wallet) {
          let cfg = this.wallets[this.group.wallet];
          if(cfg) {
            if(cfg['address'])
              exec = exec.replace(/{user}/g, '<span class="label blue">' + cfg['address'] + '</span>');
          }
        }

        if(this.groupCrypto) {
          exec = exec.replace(/{algorithm}/g, '<span class="label green">' + this.coins[this.groupCrypto].algorithm.toLowerCase() + '</span>');
        }

        if(this.isValidConfig) {
          let cfg = this.pools[this.group.pool.id].endpoints[this.group.pool.endpoint];
          if(cfg) {
            if(cfg['url']) {
              exec = exec.replace(/{pool\.url}/g, '<span class="label blue">' + ((cfg['stratum'] ? 'stratum+tcp://' : '') + cfg['url']) + '</span>');

              if(cfg['url'].indexOf(':') !== -1) {
                exec = exec.replace(/{pool\.url.host}/g, '<span class="label blue">' + ((cfg['stratum'] && !this.program.execute.ignore_stratum ? 'stratum+tcp://' : '') + cfg['url'].split(':')[0]) + '</span>');
                exec = exec.replace(/{pool\.url.port}/g, '<span class="label blue">' + cfg['url'].split(':')[1] + '</span>');
              }
            }

            if(cfg['password'])
              exec = exec.replace(/{pool\.pass}/g, '<span class="label blue">' + cfg['password'] + '</span>');

            if(cfg['username'])
              exec = exec.replace(/{user}/g, '<span class="label blue">' + cfg['username'] + '</span>');
          }
        }

        exec = exec.replace(/{worker\.id}/g, '<span class="label green">Worker-ID</span>');

        exec = exec.replace(/\B(--?[^-\s]+) ({[^\s<]+)/g, '');

        exec = exec.replace(/(--?[^-\s<{]+) ((?:<span class=".+?">|.)+?)(?= |$)/g, '$1 <span class="label orange">$2</span>');

        return '<span class="text-muted">' + this.program.execute.file + '</span> ' + exec;
      }
    },
    methods: {
      pushUpdate() {
        this.$nextTick(function() {
          this.group.algorithm = this.coins[this.groupCrypto].algorithm;

          let v = {}; v[this.id] = this.group;
          this.$socket.send('groups', 'update', v);
        });
      },

      changedProgram() {
        this.$nextTick(function() {
          this.group.program.execute = {args: this.program.execute.args };
          this.pushUpdate();
        });
      }
    }
  }
</script>



