<template>
  <v-container fluid grid-list-md>
    <v-layout row wrap>
      <v-flex xs12 md6>
        <v-flex>
          <v-card>
            <v-card-title>
              <v-layout row wrap>
                <v-flex xs12 sm10>
                  <v-text-field
                    hide-details
                    solo
                    v-model="group.name"
                    @blur="pushUpdate()">
                  </v-text-field>
                </v-flex>
                <v-flex xs6 sm1 class="text-xs-center">
                  <v-tooltip top>
                    <v-btn small flat icon slot="activator" @click="dialog.refresh.id = id; dialog.refresh.shown = true">
                      <v-icon class="indigo--text">sync</v-icon>
                    </v-btn>
                    <span>Refresh</span>
                  </v-tooltip>
                </v-flex>
                <v-flex xs6 sm1 class="text-xs-center">
                  <v-menu left>
                    <v-btn small flat icon color="primary" slot="activator"><v-icon>arrow_drop_down</v-icon></v-btn>
                    <v-list>
                      <v-list-tile @click="dialog.refresh.shown = true">
                        <v-list-tile-title><v-icon class="small indigo--text">fa-refresh</v-icon> Refresh All</v-list-tile-title>
                      </v-list-tile>
                      <v-list-tile @click="dialog.reboot.shown = true">
                        <v-list-tile-title><v-icon class="small blue--text">fa-power-off</v-icon> Reboot All</v-list-tile-title>
                      </v-list-tile>
                      <v-list-tile @click="dialog.wake.shown = true">
                        <v-list-tile-title><v-icon class="small orange--text">fa-heartbeat</v-icon> Wake Up All</v-list-tile-title>
                      </v-list-tile>
                      <v-list-tile @click="dialog.shutdown.shown = true">
                        <v-list-tile-title><v-icon class="small red--text">fa-plug</v-icon> Shutdown All</v-list-tile-title>
                      </v-list-tile>
                    </v-list>
                  </v-menu>
                </v-flex>
              </v-layout>
            </v-card-title>
            <v-card-text>
              <v-layout row wrap class="pb-2 text-xs-center">
              <v-flex xs4>
                <v-icon class="small" color="orange">fa-thermometer-empty</v-icon> {{ convertTemperature(getMaxTemperature({group: group_id}), true) }}
              </v-flex>
              <v-flex xs4>
                <v-icon class="small" color="orange">fa-hashtag</v-icon> {{ convertRate({ rate: getTotalRate({group: group_id}), display: true, algorithm: getAlgorithm({ group: group_id }) }) }}/s
              </v-flex>
              <v-flex xs4>
                <v-tooltip top>
                  <div slot="activator"><v-icon class="small" color="orange">fa-hashtag</v-icon> {{ getTotalWatts({group: group_id}).toFixed(2) }} W</div>
                  <span>{{ convertRate({ rate: getTotalRate({group: group_id}) / getTotalWatts({group: group_id}), display: true, algorithm: getAlgorithm({ group: group_id }) }) }} per Watt</span>
                </v-tooltip>
              </v-flex>
            </v-layout>
            </v-card-text>
          </v-card>
        </v-flex>
        <v-flex>
          <v-expansion-panel expand>
            <v-expansion-panel-content>
              <div slot="header">Miner Settings</div>
              <v-card>
                <v-card-text>
                  <group-settings :id="group_id"></group-settings>
                </v-card-text>
              </v-card>
            </v-expansion-panel-content>
          </v-expansion-panel>
        </v-flex>
        <v-flex>
          <v-expansion-panel expand>
            <v-expansion-panel-content>
              <div slot="header">NVIDIA Settings</div>
              <v-card>
                <v-card-text><oc-settings-nvidia v-model="group.hardware.overclock.nvidia" :changed="pushUpdate" :offset="true"></oc-settings-nvidia></v-card-text>
              </v-card>
            </v-expansion-panel-content>
            <v-expansion-panel-content>
              <div slot="header">AMD Settings</div>
              <v-card>
                <v-card-text><oc-settings-amd v-model="group.hardware.overclock.amd" :changed="pushUpdate"></oc-settings-amd></v-card-text>
              </v-card>
            </v-expansion-panel-content>
          </v-expansion-panel>
        </v-flex>
      </v-flex>
      <v-flex xs12 md6>
        <v-card>
          <v-card-text>
            <v-layout row wrap class="pa-2 text-xs-center">
              <v-flex xs6 md3>
                <v-icon class="small" color="green">fiber_manual_record</v-icon> <b>Online</b>: {{ getOnlineMiners({group: group_id}) }}
              </v-flex>
              <v-flex xs6 md3>
                <v-icon class="small" color="orange">fiber_manual_record</v-icon> <b>Unsynced</b>: {{ getUnsyncedMiners({group: group_id}) }}
              </v-flex>
              <v-flex xs6 md3>
                <v-icon class="small" color="red">fiber_manual_record</v-icon> <b>Offline</b>: {{ getOfflineMiners({group: group_id}) }}
              </v-flex>
              <v-flex xs6 md3>
                <v-icon class="small" color="grey">fiber_manual_record</v-icon> <b>Discon</b>: {{ getDisconnectedMiners({group: group_id}) }}
              </v-flex>
            </v-layout>
            <v-divider />
            <machines-table :group="this.group_id" actions />
          </v-card-text>
        </v-card>
      </v-flex>
    </v-layout>

    <v-dialog v-model="dialog.refresh.shown" max-width="400px">
      <v-card>
        <v-card-title>
          <h4 class="headline mb-0">Confirm</h4>
        </v-card-title>
        <v-card-text>
          Are you sure you want to refresh all of the group's miners?
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="grey" flat @click.stop="dialog.refresh.shown = false">No</v-btn>
          <v-btn color="primary" flat @click.stop="dialog.refresh.shown = false; doGroupAction(group_id, 'refresh')">Yes</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="dialog.wake.shown" max-width="400px">
      <v-card>
        <v-card-title>
          <h4 class="headline mb-0">Confirm</h4>
        </v-card-title>
        <v-card-text>
          Are you sure you want to wake up all of the group's miners?<br />
          <small><b>Note:</b> This only works on machines with Wake-on-LAN enabled.</small>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="grey" flat @click.stop="dialog.wake.shown = false">No</v-btn>
          <v-btn color="primary" flat @click.stop="dialog.wake.shown = false; doGroupAction(group_id, 'wake')">Yes</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="dialog.reboot.shown" max-width="400px">
      <v-card>
        <v-card-title>
          <h4 class="headline mb-0">Confirm</h4>
        </v-card-title>
        <v-card-text>
          Are you sure you want to reboot all of the group's miners?
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="grey" flat @click.stop="dialog.reboot.shown = false">No</v-btn>
          <v-btn color="primary" flat @click.stop="dialog.reboot.shown = false; doGroupAction(group_id, 'reboot')">Yes</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="dialog.shutdown.shown" max-width="400px">
      <v-card>
        <v-card-title>
          <h4 class="headline mb-0">Confirm</h4>
        </v-card-title>
        <v-card-text>
          Are you sure you want to shutdown all of the group's miners?<br />
          <small><b>Note:</b> You will have to turn them on manually if Wake-on-LAN is not enabled!</small>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="grey" flat @click.stop="dialog.shutdown.shown = false">No</v-btn>
          <v-btn color="primary" flat @click.stop="dialog.shutdown.shown = false; doGroupAction(group_id, 'shutdown')">Yes</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
  export default {
    data() {
      return {
        dialog: {
          refresh: {
            shown: false
          },
          wake: {
            shown: false
          },
          reboot: {
            shown: false
          },
          shutdown: {
            shown: false
          }
        }
      }
    },
    computed: {
      group_id() {
        return this.$route.params.id;
      },
      group() {
        return this.$store.state.groups[this.group_id];
      },
      machines() {
        let machines = {};
        for(let [id, machine] of Object.entries(this.$store.state.machines)) {
          if(machine.group == this.group_id)
            machines[id] = machine;
        }
        return machines;
      }
    },
    methods: {
      doAction(action) {
        let v = {to: 'one'}; v[this.group_id] = {id: action};
        this.$socket.send('groups', 'action', v);
      },
      pushUpdate() {
        let vm = this;
        this.$nextTick(function() {
          let v = {}; v[vm.group_id] = vm.$store.state.groups[vm.group_id];
          this.$socket.send('groups', 'update', v);
        });
      }
    }
  }
</script>



