import Vue from 'vue'
import App from './App.vue'

import VueResource from 'vue-resource';
import VueChart from 'vuechart';
import VueUp from 'vueup';
import VueToasted from 'vue-toasted';

import Vuetify from 'vuetify'
import 'vuetify/dist/vuetify.css'

import Statistics from './components/Statistics.vue'
import GroupSettings from './components/GroupSettings.vue'
import MinerStatus from './components/MinerStatus.vue'
import MachinesTable from './components/MachinesTable.vue'
import OverclockSettings_NVIDIA from './components/OverclockSettings_NVIDIA.vue'
import OverclockSettings_AMD from './components/OverclockSettings_AMD.vue'

Vue.use(VueResource);

Vue.use(Vuetify, {
  theme: { }
})

Vue.component(MinerStatus.name, MinerStatus);
Vue.component(Statistics.name, Statistics);
Vue.component(GroupSettings.name, GroupSettings);
Vue.component(MachinesTable.name, MachinesTable);
Vue.component(OverclockSettings_NVIDIA.name, OverclockSettings_NVIDIA);
Vue.component(OverclockSettings_AMD.name, OverclockSettings_AMD);

import Socket from './socket'
Vue.use(Socket);

import './css/style.css'
import './utils'
import router from './router'
import store from './store'

Vue.component(VueChart.name, VueChart);
Vue.use(VueUp);
Vue.use(VueToasted, {
  router,
  theme: 'primary',
  duration: 3000
});

new Vue({
  el: '#app',
  router,
  store,
  render: h => h(App)
})



